body {
  background-color: lightblue;
  display: flex;
  flex-direction: row-reverse;
  gap: 30px;
}
body h1 {
  color: red;
  text-align: center;
  padding: 2rem;
  background-color: initial;
  border: 2px solid magenta;
}
body h1:hover {
  color: green;
  background-color: yellowgreen;
}
body h2 {
  color: darkmagenta;
  border: 2px solid black;
}
body h2:hover {
  color: deepskyblue;
}
body div#root {
  padding: 3rem;
  margin: 2rem;
  border: 2px solid black;
}
body div#root h1 {
  color: yellow;
  background-color: yellowgreen;
  border: none;
  text-decoration: underline;
}
body div#root h1:hover {
  border: 4px dotted red;
  background-color: none;
  text-decoration: none;
}